.tygia {
	padding: 30px 0; }

.table {
	border: none;
	thead {
		th {
			font-size: 1rem;
			font-weight: 700;
			background-color: $link;
			text-align: center;
			text-transform: uppercase;
			vertical-align: middle;
			color: $white;
			border: 1px solid $white;
			&.text-xs {
				font-size: 0.875rem;
				background-color: #cdecf9;
				text-transform: none;
				color: darken($white, 80%); } } }
	td {
		padding: 0.5rem 1rem;
		vertical-align: middle;
		font-size: 0.875rem;
		border: 1px solid $white;
		&.bold {
			font-weight: 700; } } }

.block-filter {
	padding: 1rem 0;
	@include media-breakpoint-up(md) {
		padding: 1rem 0; }
	.filter-item {
		display: inline-block;
		margin-bottom: 1rem;
		@include media-breakpoint-up(md) {
			margin-right: 2rem; }
		label {
			margin: 0 .5rem 0 0;
			color: darken($white, 80%); }
		input, select {
			color: darken($white, 60%);
			height: 30px;
			line-height: 30px;
			padding: 0 1rem;
			width: 150px;
			border: 1px solid darken($white, 15%);
			@include border-radius(35px); }
		.fa.fa-calendar {
			color: $link;
			margin-left: 10px;
			@include media-breakpoint-up(md) {
				margin-left: 0.7rem; } } }
	a.filter-btn {
		height: 30px;
		line-height: 30px;
		text-align: center;
		text-decoration: none;
		background-color: $link;
		padding: 0 2rem;
		@include border-radius(35px);
		display: inline-block;
		color: $white;
		@include transition(all 0.4s);
		&:hover,
		&:focus {
			background-color: darken($link, 10%); } } }
.block-note {
	p {
		font-size: .875rem;
		font-style: italic;
		margin: 0;
		text-align: right;
		color: darken($white, 40%); }
	a.print {
		display: inline-block;
		float: right;
		text-align: center;
		margin-top: 0.5rem;
		margin-bottom: 2rem;
		.fa {
			line-height: 32px;
			width: 35px;
			height: 35px;
			text-align: center;
			color: lighten($link, 20%);
			border: 2px solid lighten($link, 20%);
			@include border-radius(50%);
			@include transition(all .3s ease-out); }
		&:hover {
			.fa {
				color: $link;
				border: 2px solid $link; } } } }

.block-table {
	.table {
		thead {
			th {
				padding: 0.5rem 1rem;
				text-transform: none; } }
		tbody {
			tr {
				td {
					font-weight: 700;
					text-align: right;
					img {
						margin-right: 0.5rem; }
					&:first-child {
						font-weight: 500;
						text-align: left; } }
				&:nth-child(even) {
					background-color: #ececec; }
				&.title {
					background-color: #cdecf9;
					color: $link;
					td {
						font-weight: 500; } } } } } }
