.block-exchangeRate {
	padding: 30px 15px;
	background-color: $white;
	margin-bottom: 15px;
	.form-group {
		margin-bottom: 15px;
		@include media-breakpoint-up(md) {
			margin-bottom: 0; }
		label {
			display: block;
			@include media-breakpoint-down(sm) {
				margin-bottom: 10px; }
			@include media-breakpoint-up(md) {
				line-height: 35px;
				margin-right: 10px;
				width: auto;
				float: left; } }
		.group {
			display: block;
			@include media-breakpoint-up(md) {
				float: left;
				width: auto; } }
		input[type="submit"] {
			margin-top: 0; }
		&.width-full {
			label {
				@include media-breakpoint-up(md) {
					width: 65px; } }
			.group {
				@include media-breakpoint-up(md) {
					width: calc(100% - 75px); } } } }
	.title-wrap {
		@include pos(rel,null);
		margin-bottom: 20px;
		&:after {
			content: '';
			display: table;
			clear: both;
			min-height: 1; }
		.title-rate {
			@include fs(26rem);
			font-weight: 500;
			line-height: 1.4;
			margin: 0;
			float: left;
			display: block; }
		.support-wrap {
			display: block;
			text-align: right;
			float: right;
			margin-top: 10px;
			@include pos(rel,null);
			a {
				@include fs(12rem);
				font-weight: 500;
				color: $white-brown;
				line-height: 1;
				margin-right: 15px;
				img {
					margin-right: 10px;
					vertical-align: top; }
				span {
					@include pos(rel,null);
					top: -2px; }
				&.icon-share:hover + .social-list {
					display: inline-block; } }
			.social-list {
				background-color: $white;
				padding: 7px 15px;
				min-width: 100px;
				box-shadow: 0 0 3px 1px rgba(0,0,0,0.3);
				@include bor-rad(5px);
				@include pos(abs,calc(100% - 5px) 0 null null);
				display: none;
				@include transition(all 0.3s);
				&:hover {
					display: block; }
				a {
					@include fs(16rem);
					font-weight: 500;
					margin: 0 10px;
					.fa.fa-facebook {
						color: $blue; }
					.fa.fa-google-plus {
						color: red; } } } } }
	table {
		border: 1px solid darken($white, 20%);
		border-collapse: collapse;
		tr {
			th {
				@include fs(13rem);
				font-weight: 500;
				line-height: 1.4;
				text-align: center;
				background-color: $link;
				color: $white;
				border-top: none; }
			td {
				color: $black;
				@include fs(13rem);
				font-weight: 500;
				text-align: center;
				border: 1px solid darken($white, 20%);
				img {
					margin-right: 5px;
					vertical-align: middle; } } } } }
.link-exchange {
	text-align: right;
	display: block;
	padding-top: 15px;
	margin-right: 10px;
	border-top: 1px solid darken($white, 10%);
	ul.nav {
		li {
			display: inline-block;
			a {
				@include fs(12rem);
				font-weight: 500;
				color: $white-brown;
				line-height: 1;
				margin-right: 15px;
				.fa {
					@include fs(16rem);
					margin-right: 5px; }
				span {
					@include pos(rel,null); } } } } }
