.vietbank-bg {
	background-color: darken($white, 3%);
	padding-bottom: 30px;
	h1 {
		color: $black;
		@include fs(24rem);
		font-weight: 700;
		line-height: 1.4;
		padding: 0 0 5px;
		margin: 30px 0;
		@include pos(rel,null);
		@include media-breakpoint-up(md) {
			margin: 60px 0 0;
			@include fs(32rem); }
		&:after {
			content: '';
			width: 30px;
			height: 2px;
			background-color: $orange;
			@include pos(abs, null null 0 0); }
		&.blue {
			color: $blue; } } }

//Read more
.read-more-state {
	display: none; }

.read-more-target {
	max-height: 0;
	font-size: 0;
	@include opacity(0);
	@include transition(all 0.4s); }

.read-more-state:checked ~ .read-more-wrap .read-more-target {
	font-size: inherit;
	max-height: 999em;
	@include opacity(1); }

.read-more-state:checked ~ .read-more-trigger {
	display: none; }

.read-more-trigger {
	color: $orange;
	@include fs(14rem);
	font-weight: 500; }

//Thu ngo
.thungo-ct {
	background: #e0e4e5;
	padding: 0;
	margin: 30px 0 0;
	@include bor-rad(3px);
	.content {
		margin: 15px;
		padding: 15px;
		background: #ecefef;
		@include bor-rad(3px);
		@include media-breakpoint-up(lg) {
			margin: 25px 0px 25px 25px;
			padding: 20px 25px;
			height: calc(100% - 50px); }
		.title {
			@include fs(24rem);
			color: $black;
			margin: 0;
			line-height: 1.4; }
		.pst {
			@include fs(17rem);
			font-weight: 500;
			line-height: 1.4;
			margin: 0 0 20px;
			color: $black; }
		.desc {
			@include fs(14rem);
			font-weight: 500;
			color: $black;
			text-align: justify;
			p {
				margin: 0 0 15px; }
			.letter {
				@include pos(rel,null);
				margin: 0 0 15px;
				font-style: italic;
				&:before {
					content: '\f10d';
					font-size: 28px;
					font-weight: 500;
					font-family: "FontAwesome";
					color: $blue;
					line-height: 1;
					margin-right: 5px; }
				&:after {
					content: '\f10e';
					font-size: 28px;
					font-weight: 500;
					font-family: "FontAwesome";
					color: $blue;
					@include pos(abs, null 0 -15px null); } } } }
	.avatar {
		text-align: center;
		display: flex;
		align-items: flex-end;
		height: 100%;
		img {
			margin: 0 auto; } }
	.btn-more {
		color: $link;
		text-decoration: none;
		@include fs(14rem);
		font-weight: 600;
		line-height: 1.4;
		display: block;
		margin: 5px 0;
		visibility: visible;
		@include transition(all 0.8s ease);
		@include opacity(1);
		&.mclose {
			@include opacity(0);
			visibility: hidden; } }
	.profile-wrapper {
		overflow-y: hidden;
		height: 650px;
		@include media-breakpoint-up(md) {
			height: 300px; }
		@include media-breakpoint-up(lg) {
			height: 500px; }
		@include media-breakpoint-up(xl) {
			height: 450px; }
		.profile-copy {
			padding: 0 5px;
			@include pos(rel,0 null null null);
			@include transition(all 0.8s ease);
			.truncate {
				display: block;
				@include transition(all 0.8s ease);
				&.show {} } } } }
