.owl-carousel {
	@include media-breakpoint-up(xl) {
		.owl-nav {
			@include opacity(0);
			@include transition(all 0.3s ease); }
		&:hover {
			.owl-nav {
				@include opacity(1); } } }
	.owl-item {
		-webkit-backface-visibility: inherit;
		-moz-backface-visibility: inherit;
		backface-visibility: inherit; } }
.home-service {
	display: block;
	width: 100%;
	margin-top: 5px;
	.service {
		margin-top: 2px;
		figure {
			width: 100%;
			@include media-breakpoint-down(xs) {
				height: 130px;
				overflow: hidden; } }
		img {
			width: 100%;
			height: 100%;
			float: left;
			@include media-breakpoint-down(xs) {
				height: auto; } }
		figcaption {
			display: block;
			width: 100%;
			height: 100%;
			background: color-rgba(darken($blue,10%),0.55);
			@include pos(abs, 0 null null 0);
			text-align: center; }
		.service-content {
			display: block;
			width: 100%;
			text-align: center;
			@include pos(abs,50% null null 0);
			@include transform(translateY(-50%)); }
		.service-name {
			color: $white;
			@include fs(20rem);
			text-transform: uppercase;
			text-align: center;
			font-weight: bold;
			margin-bottom: 5px;
			@include media-breakpoint-down(md) {
				@include fs(16rem); } }
		.link {
			width: auto;
			color: $white;
			@include fs(14rem);
			font-weight: bold;
			text-align: center;
			@include media-breakpoint-down(md) {
				@include fs(12rem); }
			&:before {
				content: "\f0da";
				font-family: 'fontawesome';
				color: $white;
				margin-top: 6px;
				margin-right: 5px; } }
		.owl-prev {
			@include pos(abs,50% null null 5px);
			@include transform(translateY(-50%));
			color: transparent;
			height: 34px;
			@include media-breakpoint-down(md) {
				left: 5px; }
			@include media-breakpoint-down(sm) {
				left: 0; }
			&:before {
				display: block;
				width: 34px;
				height: 34px;
				line-height: 28px;
				text-align: center;
				@include bor-rad(50%);
				border: 2px solid $white;
				content: "\f104";
				font-family: 'fontawesome';
				@include fs(28rem);
				color: $white;
				opacity: 0.5;
				@include transition(all,0.3s,ease);
				@include media-breakpoint-down(md) {
					border: none; }
				@include media-breakpoint-down(sm) {
					background: $white;
					opacity: 1;
					color: $blue;
					@include bor-rad(0); } }
			&:hover {
				&:before {
					color: $white;
					border-color: $white;
					opacity: 1;
					@include media-breakpoint-down(sm) {
						color: $blue; } } } }
		.owl-next {
			@include pos(abs,50% 5px null null);
			@include transform(translateY(-50%));
			color: transparent;
			height: 34px;
			@include media-breakpoint-down(md) {
				right: 5px; }
			@include media-breakpoint-down(sm) {
				right: 0; }
			&:after {
				display: block;
				width: 34px;
				height: 34px;
				float: right;
				line-height: 28px;
				text-align: center;
				@include bor-rad(50%);
				border: 2px solid $white;
				content: "\f105";
				font-family: 'fontawesome';
				@include fs(28rem);
				color: $white;
				opacity: 0.5;
				@include transition(all,0.3s,ease);
				@include media-breakpoint-down(md) {
					border: none; }
				@include media-breakpoint-down(sm) {
					background: $white;
					opacity: 1;
					color: $blue;
					@include bor-rad(0); } }
			&:hover {
				&:after {
					color: $white;
					border-color: $white;
					opacity: 1;
					@include media-breakpoint-down(sm) {
						color: $blue; } } } } } }
