.home-news {
	clear: both;
	margin-left: -5px;
	margin-right: -5px;
	margin-top: 10px;
	.news-col1, .news-col3 {
		width: 25%;
		figure {
			height: 330px;
			overflow: hidden; }
		@include media-breakpoint-down(lg) {
			figure {
				height: 300px; } }
		@include media-breakpoint-down(md) {
			width: 50%;
			figure {
				height: 200px; } }
		@include media-breakpoint-down(sm) {
			figure {
				height: 180px; } }
		@include media-breakpoint-down(xs) {
			width: 100%;
			figure {
				height: 180px; } } }
	.news-col2 {
		width: 50%;
		figure {
			height: 330px;
			overflow: hidden; }
		@include media-breakpoint-down(lg) {
			figure {
				height: 300px; } }
		@include media-breakpoint-down(md) {
			width: 50%;
			figure {
				height: 200px; } }
		@include media-breakpoint-down(sm) {
			figure {
				height: 180px; } }
		@include media-breakpoint-down(xs) {
			width: 100%;
			margin-top: 10px;
			figure {
				height: 180px; } } }
	.news-col3 {
		figure {
			height: 160px; }
		@include media-breakpoint-down(lg) {
			figure {
				height: 145px; } }
		@include media-breakpoint-down(md) {
			width: 50%;
			margin-top: 10px;
			figure {
				height: 200px; } }
		@include media-breakpoint-down(xs) {
			width: 100%;
			figure {
				height: 180px; } } }
	.news-col31 {
		margin-bottom: 10px;
		@include media-breakpoint-down(xs) {
			width: 100%;
			margin-bottom: 0; } }
	.news-col {
		float: left;
		padding: 0 5px;
		figure {
			width: 100%;
			float: left;
			@include pos(rel,null);
			a {
				width: 100%;
				float: left; } }
		img {
			width: 100%; }
		.img-overlay {
			@include pos(abs, 0 0 0 auto);
			left: 0;
			background: color-rgba(darken($blue,10%),0.40); }
		figcaption {
			width: 100%;
			height: 60px;
			background: $white;
			padding: 15px 20px;
			@include pos(abs,null null 0 0);
			@include media-breakpoint-down(lg) {
				height: 44px; }
			.promo {
				@include pos(abs, -18px null null 15px);
				color: $black;
				@include fs(12rem);
				font-weight: 600;
				background: $orange;
				padding: 3px 10px; } }
		.news-name {
			@include fs(13rem);
			color: $link;
			text-transform: uppercase;
			line-height: 1.4;
			@include media-breakpoint-down(lg) {
				text-overflow: ellipsis;
				overflow: hidden;
				white-space: nowrap;
				@include pos(rel,null);
				padding-right: 10px;
				&:after {
					@include pos(abs, 0 0 null null ); } }
			&:after {
				content: "\f0da";
				@include fs(18rem);
				line-height: 13px;
				font-family: 'fontawesome';
				margin-left: 10px; } } } }
