.home-link {
	width: 100%;
	margin: 30px 0 15px 0;
	.link-col {
		border-left: 1px solid darken($white,10%);
		padding: 0 30px;
		@include media-breakpoint-down(lg) {
			padding: 10px 15px; }
		@include media-breakpoint-down(sm) {
			padding: 0 15px;
			border: none; }
		@include media-breakpoint-down(xs) {
			padding: 0 15px; }
		&:first-child {
			border: none; }
		.link-img {
			display: table-cell;
			height: 63px;
			width: 58px;
			vertical-align: middle;
			@include media-breakpoint-down(lg) {
				width: 40px;
				height: auto; }
			@include media-breakpoint-down(md) {
				width: 30px;
				height: auto;
				img {
					width: 100%; } }
			@include media-breakpoint-down(xs) {
				display: none; } }
		.link {
			display: table-cell;
			width: auto;
			@include fs(16rem);
			font-weight: bold;
			color: $link;
			vertical-align: middle;
			padding-left: 20px;
			@include media-breakpoint-down(md) {
				@include fs(12rem);
				padding-left: 10px; }
			@include media-breakpoint-down(xs) {
				padding: 5px 0; }
			&:after {
				content: "\f0da";
				font-family: 'fontawesome';
				margin-left: 10px; } } }
	.download-app {
		border-left: 1px solid darken($white,10%);
		a {
			float: left; }
		.device, .ios, .android {
			float: left; }
		.device {
			width: 50%;
			// +media-breakpoint-down(xs)
 }			// 	width: 100%
		.ios {
			width: 50%;
			// +media-breakpoint-down(xs)
 }			// 	width: 100%
		.android {
			width: 50%;
			// +media-breakpoint-down(xs)
 }			// 	width: 100%
		@include media-breakpoint-down(sm) {
			border-left: 1px solid darken($white,10%);
			padding: 10px 15px; }
		@include media-breakpoint-down(xs) {
			padding: 30px 15px; } } }
