.form-group {
	margin-bottom: 10px;
	@include pos(rel,null);
	&:after {
		content: '';
		display: table;
		clear: both;
		min-height: 1; }
	label {
		@include fs(11rem);
		font-weight: 600;
		line-height: 1.5;
		display: block;
		@include media-breakpoint-up(md) {
			display: inline-block;
			width: 125px; } }
	.group {
		width: 100%;
		@include pos(rel,null);
		@include media-breakpoint-up(md) {
			display: inline-block;
			width: calc(100% - 330px); }
		.form-control, select.form-control {
			width: 100%;
			height: 35px!important;
			line-height: 35px;
			padding: 0 10px;
			@include fs(13rem);
			font-weight: 500;
			color: $black; }
		select.form-control {
			-webkit-appearance: none;
			-moz-appearance: none;
			appearance: none;
			background: transparent url("../img/arrow_select.png") no-repeat top right; }
		.unit, .icon {
			@include pos(abs,5px 10px null null);
			@include fs(13rem); } }
	input[type="submit"] {
		width: 185px;
		@include fs(13rem);
		font-weight: 500;
		line-height: 1;
		display: inline-block;
		padding: 10px 15px;
		color: $black;
		background-color: $orange;
		text-align: center;
		border: none;
		margin-top: 5px;
		float: right;
		@include bor-rad(35px);
		&:hover {
			background-color: darken($orange, 5%); } } }
.block-bieuMau {
	padding: 30px 15px;
	background-color: $white;
	.item-mauphi {
		padding-bottom: 20px;
		margin-bottom: 20px;
		border-bottom: 1px dashed darken($white, 30%);
		@include pos(rel,null);
		.title {
			@include fs(13rem);
			font-weight: 600;
			line-height: 1.4;
			text-transform: uppercase;
			color: $link;
			margin: 0 0 5px;
			padding-left: 30px;
			border-bottom: none;
			@include pos(rel,null);
			&:before {
				content: '\f0a9';
				@include fs(16rem);
				font-family: "FontAwesome";
				font-weight: 500;
				line-height: 1;
				@include pos(abs,0 null null 0); } }
		ul {
			padding-left: 30px;
			li {
				a {
					@include fs(13rem);
					font-weight: 500;
					line-height: 1.4;
					color: $black;
					@include pos(rel,null);
					margin: 0 0 10px;
					padding-left: 15px;
					@include transition(all 0.3s);
					&:before {
						content: '\f105';
						@include fs(13rem);
						font-family: "FontAwesome";
						font-weight: 500;
						line-height: 1;
						@include pos(abs, 0 null null 0); }
					.icon {
						@include fs(12rem);
						background-color: #989898;
						color: $white;
						padding: 1px 4px;
						display: inline-block;
						margin-left: 10px;
						@include bor-rad(3px); }
					&:hover {
						color: $orange;
						.icon {
							background-color: $orange;
							color: $white; } } } } } } }
//Cong cu tinh toan
.block-calculatorTool {
	background-color: $white;
	padding: 20px 15px;
	margin-bottom: 15px;
	.form-group {
		margin-bottom: 10px;
		@include pos(rel,null);
		&:after {
			content: '';
			display: table;
			clear: both;
			min-height: 1; }
		label {
			@include fs(13rem);
			font-weight: 600;
			line-height: 35px;
			width: 130px;
			display: inline-block; }
		.group {
			display: inline-block;
			width: calc(100% - 135px);
			@include pos(rel,null);
			.form-control {
				height: 35px;
				line-height: 35px;
				padding: 0 10px;
				@include fs(13rem);
				font-weight: 500;
				color: $black; }
			.unit {
				@include pos(abs,5px 10px null null);
				@include fs(13rem); } }
		textarea {
			min-height: 125px;
			@include fs(14rem);
			width: 100%;
			color: $black;
			background-color: #f4f4f4;
			padding: 10px 15px;
			border: none; }
		input[type="submit"] {
			@include fs(13rem);
			font-weight: 400;
			line-height: 1;
			display: inline-block;
			padding: 8px 50px;
			color: $black;
			background-color: $orange;
			text-align: center;
			border: none;
			float: right;
			@include bor-rad(35px);
			&:hover {
				background-color: darken($orange, 5%); } } }
	.title-wrap {
		@include pos(rel,null);
		margin-bottom: 20px;
		&:after {
			content: '';
			display: table;
			clear: both;
			min-height: 1; }
		.title-tool {
			@include fs(26rem);
			font-weight: 500;
			line-height: 1.4;
			margin: 0;
			float: left;
			display: block; }
		.support-wrap {
			display: block;
			text-align: right;
			float: right;
			margin-top: 10px;
			a {
				@include fs(12rem);
				font-weight: 500;
				color: $white-brown;
				line-height: 1;
				margin-right: 15px;
				img {
					margin-right: 10px;
					vertical-align: top; }
				span {
					@include pos(rel,null);
					top: -2px; } } } }
	table {
		border: 1px solid darken($white, 20%);
		border-collapse: collapse;
		tr {
			th {
				@include fs(13rem);
				font-weight: 500;
				line-height: 1.4;
				text-align: center;
				background-color: $link;
				color: $white;
				border-top: none; }
			td {
				color: $black;
				@include fs(13rem);
				font-weight: 500;
				text-align: center;
				border: 1px solid darken($white, 20%); }
			&.sum {
				td {
					background-color: #f5f5f5;
					@include fs(13rem);
					font-weight: 600;
					line-height: 1.4;
					color: $link; } } } } }
.block-borrowTool {
	padding: 30px 15px;
	background-color: $white;
	.form-group {
		label {
			@include pos(rel,null);
			@include media-breakpoint-down(sm) {
				margin-bottom: 10px; }
			@include media-breakpoint-up(md) {
				top: 6px; } } }
	.desc {
		@include fs(13rem);
		font-weight: 500;
		line-height: 1.4;
		padding-bottom: 10px;
		margin-bottom: 40px;
		border-bottom: 1px solid darken($white, 15%); }
	.result {
		padding: 15px;
		background-color: darken($white, 10%);
		h3 {
			@include fs(20rem);
			font-weight: 500;
			line-height: 1.4;
			margin: 0 0 15px; } }
	.item-result {
		@include fs(13rem);
		font-weight: 500;
		line-height: 1.5; } }
.block-revive {
	padding: 30px 15px;
	background-color: $white;
	.form-group {
		label {
			width: 85px;
			@include fs(13rem);
			@include media-breakpoint-down(sm) {
				width: 100%;
				margin-bottom: 10px; } }
		.group {
			@include media-breakpoint-up(md) {
				width: calc(100% - 90px); } } }
	.title-deposits {
		@include fs(13rem);
		font-weight: 700;
		margin: 0;
		line-height: 1.4;
		margin: 0 0 10px; }
	.box-deposits {
		padding: 20px 15px;
		background-color: darken($white, 5%);
		@include media-breakpoint-down(sm) {
			margin-bottom: 10px; }
		.checkbox {
			margin: 0 0 5px;
			label {
				@include fs(13rem);
				font-weight: 500;
				line-height: 1.4; } } }
	.title-table {
		@include fs(13rem);
		font-weight: 700;
		line-height: 1.4;
		margin: 0 0 10px;
		text-transform: uppercase;
		display: block;
		color: $link; }
	table {
		border: 1px solid darken($white, 25%);
		border-collapse: collapse;
		@include bor-rad(5px);
		tr {
			th {
				@include fs(12rem);
				font-weight: 500;
				line-height: 1.4;
				text-align: center;
				background-color: $link;
				text-transform: none;
				padding: 15px 10px;
				color: $white;
				border: none;
				white-space: nowrap; }
			td {
				color: $black;
				@include fs(13rem);
				font-weight: 500;
				text-align: center;
				border: 1px solid darken($white, 25%); }
			&:nth-child(even) {
				td {
					background-color: #f6f6f6; } } } }
	.result {
		padding: 15px 30px;
		background-color: #f6f6f6;
		@include bor-rad(5px);
		h3 {
			@include fs(20rem);
			font-weight: 500;
			line-height: 1.4;
			margin: 0 0 20px; } }
	.item-result {
		@include fs(13rem);
		font-weight: 500;
		line-height: 1.5;
		margin-bottom: 5px;
		strong {
			display: inline-block;
			width: 130px; } } }
