footer {
	display: block;
	width: 100%; }
.footer-top {
	display: block;
	width: 100%;
	// background: linear-gradient(to right,#3493cd,#9bd8f5)
	background: $bg1;
	@include pos(rel,null);
	.container {
		@include pos(rel,null);
		// background: url(../img/footer-bg.png) no-repeat top right
		background-size: 40% auto;
		padding-bottom: 50px;
		@include media-breakpoint-down(md) {
			background-position: bottom right; }
		@include media-breakpoint-down(sm) {
			padding-bottom: 30px; }
		@include media-breakpoint-down(xs) {
			padding-bottom: 20px; } }
	.footer-link {
		width: 100%;
		margin-top: 50px;
		@include media-breakpoint-down(md) {
			text-align: left; }
		@include media-breakpoint-down(sm) {
			text-align: center;
			margin-top: 30px; }
		@include media-breakpoint-down(xs) {
			text-align: left;
			margin-top: 20px; } }
	.title {
		@include fs(15rem);
		font-weight: bold;
		color: $black;
		&:after {
			display: block;
			content: "";
			width: 120px;
			height: 1px;
			background: $link;
			opacity: 1;
			margin-top: 15px;
			@include media-breakpoint-down(md) {
				margin: 0;
				margin-top: 15px;
				clear: both; }
			@include media-breakpoint-down(sm) {
				margin: 0 auto;
				margin-top: 15px;
				width: 200px; }
			@include media-breakpoint-down(xs) {
				width: 120px; } } }
	nav.link {
		margin-top: 15px;
		@include media-breakpoint-down(md) {
			margin-top: 15px; }
		ul {
			li {
				@include fs(13rem);
				line-height: 1.5;
				margin-bottom: 7px;
				@include media-breakpoint-down(md) {
					line-height: 1.5; }
				a {
					color: $white-black;
					opacity: 0.8;
					@include transition(all,0.3s);
					&:hover {
						opacity: 1;
						color: $link; } } } } }
	.register-email {
		margin-top: 50px;
		@include media-breakpoint-down(md) {
			text-align: center; }
		@include media-breakpoint-down(xs) {
			margin-top: 20px; }
		.title {
			&:after {
				@include media-breakpoint-down(md) {
					margin: 0 auto;
					margin-top: 15px; } } }
		.content {
			@include fs(13rem);
			color: $white-black;
			margin-top: 30px;
			opacity: 0.8;
			@include media-breakpoint-down(md) {
				margin-top: 20px; }
			@include media-breakpoint-down(xs) {
				@include fs(12rem); } }
		.form-group {
			width: 85%;
			margin: 0;
			margin-top: 20px;
			padding: 0;
			@include pos(rel,null);
			@include media-breakpoint-down(lg) {
				width: 95%; }
			@include media-breakpoint-down(md) {
				width: 70%;
				margin: 0 auto;
				margin-top: 20px; }
			@include media-breakpoint-down(xs) {
				width: 100%;
				margin-top: 10px; }
			label {
				display: none; }
			input {
				width: 100%;
				background: $white;
				@include bor-rad(100px);
				padding: 13px 20px;
				@include fs(14rem);
				color: darken($white,60%);
				border: none; }
			input[type="submit"] {
				@include pos(abs,0 0 0 null);
				background: $btn;
				color: $black;
				width: auto;
				@include fs(15rem);
				font-weight: bold;
				@include transition(all,0.3s);
				&:hover {
					background: darken($btn, 5%); } } }
		.social {
			margin-top: 20px;
			width: auto;
			@include media-breakpoint-down(md) {
				margin: 0 auto;
				margin-top: 20px; }
			@include media-breakpoint-down(xs) {
				margin-top: 10px; }
			.fa {
				@include fs(22rem);
				color: $link;
				@include media-breakpoint-down(md); }
			a {
				display: block;
				float: left;
				margin-left: 10px;
				opacity: 1;
				width: 45px;
				height: 45px;
				line-height: 47.5px;
				text-align: center;
				border: 2px solid $link;
				@include bor-rad(50%);
				@include transition(all,0.3s);
				@include media-breakpoint-down(md) {
					display: inline-block;
					float: none; }
				&:hover {
					opacity: 1; }
				&:first-child {
					margin: 0; } } } }
	.livesupport {
		// display: none
		width: 190px;
		height: 35px;
		background: url(../img/livesupport.png) center center no-repeat;
		@include pos(abs, null 15px 0 null); } }
.copyright {
	width: 100%;
	background: darken($blue,7%);
	@include fs(13rem);
	color: $white;
	padding: 15px 0 16px;
	@include media-breakpoint-down(lg) {
		text-align: center; }
	.location {
		float: left;
		p {
			line-height: 1.5; }
		@include media-breakpoint-down(lg) {
			width: 100%;
			line-height: 1.5;
			color: lighten($blue,40%); } }
	.copyright-content {
		float: right;
		@include media-breakpoint-down(lg) {
			width: 100%;
			line-height: 1rem;
			color: lighten($blue,40%);
			margin-top: 10px; } } }
.up {
	display: block;
	width: 50px;
	height: 50px;
	background: url(../img/up.png) no-repeat center center;
	@include pos(fix, null 30px 0 null);
	z-index: 10;
	cursor: pointer;
	@include media-breakpoint-down(xs) {
		width: 40px;
		height: 40px;
		background-size: 100% 100%;
		right: 0; } }
