.banner-section {
	display: block;
	width: 100%;
	.banner {
		img {
			width: 100%;
			@include media-breakpoint-down(lg) {
				max-width: unset;
				height: 350px; }
			@include media-breakpoint-down(md) {
				height: 300px; }
			@include media-breakpoint-down(sm) {
				height: 250px; }
			@include media-breakpoint-down(xs) {
				height: auto; } }
		.owl-nav {
			display: none;
			@include media-breakpoint-down(md) {
				display: block; } }
		.owl-prev {
			color: transparent;
			@include pos(abs,50% null null auto);
			@include transform(translateY(-50%));
			@include media-breakpoint-down(md) {
				left: -15px; }
			@include media-breakpoint-down(xs) {
				@include transform(translateY(0));
				top: 15px; }
			&:before {
				content: "\f104";
				font-family: 'fontawesome';
				@include fs(48rem);
				line-height: 48px;
				color: darken($blue,10%);
				float: right;
				@include transition(all,0.3s,ease); }
			&:hover {
				&:before {
					color: $blue; } } }
		.owl-next {
			color: transparent;
			@include pos(abs,50% 0 null null);
			@include transform(translateY(-50%));
			@include media-breakpoint-down(md) {
				right: -15px; }
			@include media-breakpoint-down(xs) {
				@include transform(translateY(0));
				top: 15px; }
			&:before {
				content: "\f105";
				font-family: 'fontawesome';
				@include fs(48rem);
				line-height: 48px;
				color: darken($blue,10%);
				float: left;
				@include transition(all,0.3s,ease); }
			&:hover {
				&:before {
					color: $blue; } } }
		.owl-dots {
			@include pos(abs, null null 20px 50%);
			@include transform(translateX(-50%));
			@include media-breakpoint-down(md) {
				display: none; }
			.owl-dot {
				width: 11px;
				height: 11px;
				float: left;
				margin: 0 4px;
				background: $white;
				@include bor-rad(50%);
				&.active {
					background: $yellow; } } }
		.caption {
			color: $blue;
			@include pos(abs, null null 150px 0);
			width: 100%;
			@include media-breakpoint-down(md) {
				bottom: 20px;
				h3, h4, h5 {
					padding-left: 20px;
					background: color-rgba($white,0.5);
					margin: 0; } }
			@include media-breakpoint-down(sm) {
				margin: 0 30px; }
			@include media-breakpoint-down(xs) {
				@include pos(static,null);
				background: $white;
				margin: 0;
				h3,h4,h5 {
					padding: 0; } }
			h4 {
				@include fs(30rem);
				font-weight: 500;
				@include media-breakpoint-down(md) {
					@include fs(20rem);
					padding-top: 15px; }
				@include media-breakpoint-down(xs) {
					@include fs(16rem); } }
			h3 {
				@include fs(30rem);
				font-weight: bold;
				margin: 8px 0;
				@include media-breakpoint-down(md) {
					@include fs(24rem);
					margin: 0; }
				@include media-breakpoint-down(xs) {
					@include fs(18rem);
					margin: 5px 0; } }
			h5 {
				@include fs(16rem);
				line-height: 24px;
				@include media-breakpoint-down(md) {
					@include fs(12rem);
					padding-bottom: 15px;
					line-height: 18px;
					padding-right: 20px; }
				@include media-breakpoint-down(xs) {
					padding-right: 0; } } } } }
.banner-wrap {
	@include pos(rel,null);
	.imageBanner {
		.imgBg {
			background-position: 100% 50%;
			background-repeat: no-repeat;
			min-height: auto;
			background-size: cover;
			line-height: 0;
			vertical-align: middle;
			width: 100%;
			display: inline-block;
			padding: 15px 0 30px;
			min-height: 160px;
			@include pos(rel,null);
			/* &:after
				content: ''
				background-color: rgba(0, 57, 94, 0.2)
				top: 0
				bottom: 0
				left: 0
				right: 0
				z-index: 1
				position: absolute
				width: 100%
				height: 100% */
			@include media-breakpoint-up(sm) {
				background-position: 50% 50%;
				background-repeat: no-repeat; }
			@include media-breakpoint-up(md) {
				background-position: 50% 0;
				background-repeat: no-repeat;
				padding: 0 0 30px 0;
				min-height: 210px; }
			@include media-breakpoint-up(lg) {
				background-position: center center;
				background-repeat: no-repeat;
				background-size: cover;
				min-height: 250px;
				padding: 0;
				&:after {
					display: none; } }
			@include media-breakpoint-up(xl) {
				min-height: 285px; }
			@media only screen and (min-width: 1600px) {
				min-height: 355px; }
			@media only screen and (min-width: 2560px) {
				min-height: 450px; } } }
	.banner-page {
		margin: 0 0 5px;
		@include media-breakpoint-up(md) {
			margin: 0; }
		.desc {
			text-align: left;
			@include pos(rel,null);
			z-index: 9;
			margin-bottom: 15px;
			@include media-breakpoint-up(md) {
				margin-top: 40px;
				@include pos(abs,0 0 null 0); }
			@include media-breakpoint-up(lg) {
				margin-top: 60px; }
			h1, .title {
				@include fs(20rem);
				font-weight: 700;
				line-height: 1.2;
				margin: 10px 0 5px;
				color: $black;
				@include media-breakpoint-up(md) {
					margin: 0 0 5px;
					line-height: 1.4;
					@include fs(22rem);
					color: $white; }
				@include media-breakpoint-up(lg) {
					@include fs(32rem);
					margin: 0 0 10px; }
				&.bottom {
					@include pos(rel, null);
					padding: 0 0 5px;
					&:after {
						content: '';
						width: 30px;
						height: 2px;
						background-color: $orange;
						@include pos(abs, 100% null null 0); } } }
			.content {
				@include fs(13rem);
				font-weight: 500;
				line-height: 1.5;
				color: $black;
				display: block!important;
				@include media-breakpoint-up(md) {
					color: $white; } } } } }
